import React, { useState } from "react";
import logo from "./logo.svg";
import "./App.css";
import { Layout, Button, Row, Col, Drawer } from "antd";
import Sidebar from "./layouts/sidebar/sidebar";
import Content from "./layouts/content";
import { Router as BrowserRouter } from "react-router-dom";
import { ApolloProvider } from "@apollo/react-hooks";
import { client } from "./graphql";
import Title from "antd/lib/typography/Title";
import { createBrowserHistory } from "history";
import { UserOutlined, MenuFoldOutlined as MenuIcon } from "@ant-design/icons";
import { browserHistory, appTitle } from "./config";
import { links } from "./links";

export const TitleContext = React.createContext({});

function App() {
  const [title, setValue] = useState(appTitle);
  const [drawer, setDrawer] = useState(false);
  return (
    <TitleContext.Provider value={{ title, setValue }}>
      <ApolloProvider client={client}>
        <BrowserRouter history={browserHistory}>
          <Layout style={{ height: "100%" }}>
            <div className="sidebarContainer">
              <Sidebar color="dark" links={links}></Sidebar>
            </div>
            <Drawer
              width={300}
              visible={drawer}
              className="drawer"
              closable={false}
              placement="left"
              onClose={() => setDrawer(false)}
            >
              <Sidebar color="dark" links={links}></Sidebar>
            </Drawer>

            <Layout>
              <Layout.Header
                style={{
                  // boxShadow: "1px 1px 2px 1px gray",
                  backgroundColor: "white",
                  padding: 0,
                }}
              >
                <Row
                  justify="space-between"
                  align="middle"
                  style={{ width: "100%", paddingRight: 16, paddingLeft: 16 }}
                >
                  <Col span={20} style={{ textAlign: "left" }}>
                    <div
                      style={{
                        display: "flex",
                        alignItems: "baseline",
                        lineHeight: 0,
                      }}
                    >
                      <Title
                        level={3}
                        //style={{ fontFamily: "Arial", lineHeight: 0 }}
                      >
                        {title}
                      </Title>
                    </div>
                  </Col>
                  <Col span={4} style={{ textAlign: "right" }}>
                    <Button
                      onClick={() => {
                        setDrawer(true);
                      }}
                      className="showMenu"
                      icon={<MenuIcon />}
                      style={{ marginLeft: "auto" }}
                    />
                  </Col>
                </Row>
              </Layout.Header>
              <Content />
            </Layout>
          </Layout>
        </BrowserRouter>
      </ApolloProvider>
    </TitleContext.Provider>
  );
}

export default App;
