import gql from "graphql-tag";

export const ALL_ENGINES = gql`
  query ALL_ENGINES(
    $limit: Int
    $offset: Int
    $matricule__icontains: String
    $year: Int
    $marque__icontains: String
    $model: ID
  ) {
    response: all_engines(
      matricule__icontains: $matricule__icontains
      marque__icontains: $marque__icontains
      year__icontains: $year
      model: $model
    ) {
      results(limit: $limit, offset: $offset) {
        id
        marque
        matricule
        model {
          id
          designation
        }
        nature
        year
      }
      totalCount
    }
  }
`;
