import React, { ReactElement, useState } from "react";
import {
  Modal,
  Row,
  Checkbox,
  Col,
  Divider,
  Radio,
  Form,
  Button,
  Input,
} from "antd";
import axios from "axios";
import { type } from "os";
import { OperationVariables } from "apollo-boost";
import { stringify } from "querystring";
import { serverBaseURL } from "../../config";

export interface ExportProps {
  fields?: { name: string | undefined; title: string | undefined }[];
  mode?: "all" | "page" | "filterd";
  visible: boolean;
  onClose: () => void;
  page?: number;
  rowPerPage?: number;
  variables?: OperationVariables;
  filename?: string;
  url?: string;
  appName: string;
  modelName: string;
}

export default function Export({
  onClose,
  visible,
  fields,
  page = 0,
  rowPerPage,
  variables,
  filename = "data",
  url = "export/xls",
  appName,
  modelName,
}: ExportProps): ReactElement {
  const [checked, setChecked] = useState<
    { name: string | undefined; title: string | undefined }[]
  >(fields ? fields.map((f) => ({ ...f })) : []);

  const [items, setItems] = useState("all");
  const [selection, setSelection] = useState("all");
  const [file, setFilename] = useState(filename);
  return (
    <form action={`${serverBaseURL}/${url}`} id="form" method="get">
      <input hidden name="items" value={items} />
      <input hidden name="selection" value={selection} />
      <input hidden name="variables" value={JSON.stringify(variables)} />
      <input hidden name="filename" value={file} />
      <input hidden name="app_name" value={appName} />
      <input hidden name="model_name" value={modelName} />
      <input
        hidden
        name="fields"
        value={checked.map((f) => f.name).toString()}
      />
      <input
        hidden
        name="headers"
        value={checked.map((f) => f.title).toString()}
      />
      {/* <input hidden name="helpers" value={JSON.stringify(checked)} /> */}

      <input hidden name="page" value={page} />
      <input hidden name="rowPerPage" value={rowPerPage} />
      <Modal
        width={800}
        destroyOnClose
        visible={visible}
        footer={
          <div>
            <Button
              onClick={() => {
                onClose();
              }}
            >
              fermer
            </Button>
            <Button type="primary" htmlType="submit" form="form">
              exporter
            </Button>
          </div>
        }
        okText="exporter"
        cancelText="fermer"
        onCancel={() => onClose()}
        title="Exporter"
      >
        <Row gutter={[10, 10]}>
          <Col span={24}>
            <h3>
              <u>Colonne à exporter</u>
            </h3>
          </Col>
          {fields?.map((f) => (
            <Col span={8}>
              <Checkbox
                checked={checked.findIndex((i) => i.name == f.name) !== -1}
                onChange={(e) => {
                  if (e.target.checked && f.name) setChecked([...checked, f]);
                  else if (!e.target.checked && f.name)
                    setChecked(checked.filter((c) => c.name !== f.name));
                }}
              >
                {f.title}
              </Checkbox>
            </Col>
          ))}
        </Row>
        <br />
        <Row>
          <Col span={24}>
            <h3>
              <u>Nombre des items</u>
            </h3>
          </Col>
          <hr />{" "}
          <Col span={24}>
            <Radio.Group
              value={items}
              onChange={(v) => setItems(v.target.value)}
            >
              <Radio value="all">Tout</Radio>
              <Radio value="displayed">Elements affichés</Radio>
            </Radio.Group>
          </Col>
        </Row>
        <br />
        <Row>
          <Col span={24}>
            <h3>
              <u>Selection</u>
            </h3>
          </Col>
          <hr />{" "}
          <Col span={24}>
            <Radio.Group
              value={selection}
              onChange={(v) => setSelection(v.target.value)}
            >
              <Radio value="all">Tout</Radio>
              <Radio value="filter">Resultats de la recherce</Radio>
            </Radio.Group>
          </Col>
        </Row>
        <br />
        <Row>
          <Form.Item label="Nom du fichier (Excel)">
            <Input
              placeholder="nom du fichier"
              value={file}
              onChange={(e) => setFilename(e.target.value)}
            />{" "}
          </Form.Item>{" "}
        </Row>
      </Modal>
    </form>
  );
}
