export const serverPort = isBuild ? 80 : 8000;
export const serverBaseURL = `http${isBuild ? "" : ""}://${
  window.location.hostname
}:${serverPort}`;

export const appTitle: string = "Rail Transport";
export const serverUrl: string = `http${isBuild ? "s" : ""}://${
  window.location.hostname
}${isBuild ? "" : `:${serverPort}`}/gql`;
